body {font-family: Arial, Helvetica, sans-serif;}
* {box-sizing: border-box;}

/* Button used to open the chat form - fixed at the bottom of the page */
.link .open-button {
  background-color: slategray;
  color: white;
  padding: 16px 20px;
  border: none;
  cursor: pointer;
  opacity: 0.8;
  position: fixed;
  bottom: 23px;
  right: 28px;
  width: 280px;
}

.userList {
  display: none;
  position: fixed;
  bottom: 0;
  right: 15px;
  border: 3px solid #f1f1f1;
  z-index: 0;
}

/* The popup chat - hidden by default */
.chat {
  display: none;
  position: fixed;
  bottom: 0;
  border: 3px solid #f1f1f1;
  z-index: 9;
  right: 336px;
}

/* Add styles to the form container */
.container {
  max-width: 300px;
  padding: 10px;
  background-color: white;
}

/* Full-width textarea */
.container textarea {
  width: 100%;
  padding: 15px;
  margin: 5px 0 22px 0;
  border: none;
  background: #f1f1f1;
  resize: none;
  min-height: 100px;
}

/* When the textarea gets focus, do something */
.container textarea:focus {
  background-color: #ddd;
  outline: none;
}

/* Set a style for the submit/send button */
.container .btn {
  background-color: #ff7779;
  color: white;
  padding: 16px 20px;
  border: none;
  cursor: pointer;
  width: 100%;
  margin-bottom:10px;
  opacity: 0.8;
}

/* Add a red background color to the cancel button */
.container .cancel {
  background-color: slategray;
}

/* Add some hover effects to buttons */
.container .btn:hover, .open-button:hover {
  opacity: 1;
}

.right {
  float: right;
}

ul {
  overflow-y: auto;
  margin-bottom: 5px;
  list-style-type: none;
  padding: 5px 5px;
  height: 200px;
  border: solid rgba(0, 0, 0, 0.137) 2.5px;
  border-radius: 1px;
}
li {
  padding-bottom: 10px;
}